<template>
  <div class="workbench">
    <div class="header">
      <public-head title="员工工作台"></public-head>
    </div>
    <div class="workbench-main">
      <div class="workbench-content">
        <!-- 内容头部标题 -->
        <dir class="title-list">
          <div class="title-item">
            <div class="title-icon">
              <img src="../../assets/img/staffOnly/ico_data_customers.png" alt="">
            </div>
            <div class="title-text">
              <div class="title-font">
                收到客户信息(条)
              </div>
              <div class="title-num">
                {{conLength}}
              </div>
            </div>
          </div>
          <div class="title-item">
            <div class="title-icon">
              <img src="../../assets/img/staffOnly/ico_data_pics.png" alt="">
            </div>
            <div class="title-text">
              <div class="title-font">
                已上传照片(张)
              </div>
              <div class="title-num">
                {{imgLength}}
              </div>
            </div>
          </div>
        </dir>
        <!-- 内部按钮列表 -->
        <div class="btn-list">
          <router-link to="/customerInformation" class="btn-item">
            <img src="../../assets/img/staffOnly/ico_view_customers.png" alt="">
            查看客户信息
          </router-link>
          <router-link to="/managePhotos" class="btn-item">
            <img src="../../assets/img/staffOnly/ico_manage_pics.png" alt="">
            管理照片
          </router-link>
          <div @click.stop="isUpload = true" class="btn-item">
            <img src="../../assets/img/staffOnly/ico_upload_pics.png" alt="">
            上传照片
          </div>
        </div>
      </div>
    </div>
    <div v-show="isUpload" class="wrapper upload-photos-wrapper" @click.stop="isUpload = false">
      <div class="upload-photos-method">
        <div class="photograph">
          拍照
          <input type="file" class="input-file" @change.stop="changeImage($event)" accept="image/*" capture="camera">
        </div>
        <div class="local-upload">
          从相册上传
          <input type="file" class="input-file" @change.stop="changeImage($event)" accept="image/*" multiple>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PublicHead from '../../components/publicHead.vue';

export default {
  data() {
    return {
      isUpload: false,
      conLength: '',
      imgLength: ''
    }
  },
  methods: {
    changeImage(e){
      let file = e.target.files;
      if(file.length > 9){
        console.log(123);
        return;
      }
      this.$router.push({
        name: 'uploadConfirm',
        params: file
      })
    },
    async getLength(){
      try {
        let res = await this.$api.staffOnly.getLength({
          exhibitionID: ''
        })
        console.log(res);
        if(res.statusCode === 75200){
          this.conLength = res.result.conLength;
          this.imgLength = res.result.imgLength;
        }
        else{
          this.$zero.Toast('服务器异常，请稍后重试');
        }
        
      } catch (err) {
        this.$zero.Toast('服务器异常，请稍后重试');
      }
    }
  },
  created() {
    this.getLength();
  },
  components: {
    PublicHead
  }
}
</script>

<style scoped>
