  width: 100%;
  padding: 2.9rem 0.2rem 0;
  /* position: relative;
  bottom: 0;
  left: 0; */
}
.workbench-content {
  width: 100%;
  background: url("../../assets/img/contactUs/bg_popup.png") no-repeat;
  background-size: 100% 100%;
}
.title-list {
  display: flex;
  padding: 0.5rem 0;
  border-bottom: 1px solid #009df1;
  margin: 0;
}
.title-item {
  flex: 1;
  padding: 0 0.4rem;
  height: 0.9rem;
  display: flex;
  align-items: center;
}
.title-item:last-child {
  border-left: 1px solid #1a5c7a;
}

.title-icon {
  width: 0.81rem;
  height: 0.8rem;
}
.title-icon > img {
  width: 100%;
  height: 100%;
}
.title-text {
  flex: 1;
  height: 0.9rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
}
.title-item:first-child .title-text {
  padding-left: 0.05rem;
}
.title-item:last-child .title-text {
  padding-left: 0.2rem;
}
.title-font {
  font-size: 0.24rem;
  color: #00edff;
}
.title-num {
  font-size: 0.4rem;
  font-weight: bold;
  color: #fff;
}
.btn-list {
  padding: 0.9rem 0.55rem 0.5rem;
}
.btn-item {
  display: block;
  width: 100%;
  height: 1.64rem;
  box-sizing: border-box;
  color: #fff;
  font-size: 0.32rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background: url("../../assets/img/index/btn_0.png") no-repeat;
  background-size: 100% 100%;
  padding-top: 0.25rem;
  padding-bottom: 0.3rem;
  margin-bottom: 0.3rem;
}
.btn-item > img {
  width: 0.61rem;
  height: 0.61rem;
}
.upload-photos-wrapper {
  display: flex;
  flex-direction: column-reverse;
  padding: 0 0.2rem;
}
.upload-photos-method {
  width: 100%;
  background: url("../../assets/img/contactUs/bg_popup.png") no-repeat;
  background-size: 100% 100%;
  color: #fff;
  font-size: 0.3rem;
  padding: 0 0.2rem;
}
.photograph,
.local-upload {
  width: 100%;
  height: 1.3rem;
  line-height: 1.3rem;
  text-align: center;
  position: relative;
}
.upload-photos-method .input-file{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.photograph {
  border-bottom: 1px solid #1a3c5c;
}
</style>